.orgo-back{
  position: absolute;
  top: 0px;
  right: 0;
  z-index: 1;
  @media (max-width:500px) {
    top: 264px;
  }
}
.orgo {
  position: relative;
  z-index: 2;
  
  
  
  // .orgo__inner

  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width:768px) {
      flex-direction: column;
    }
  }

  // .orgo__text

  &__text {
    @media (max-width:768px) {
      text-align: center;
      margin-bottom: 29px;
    }
  }

  &__text-info {
    font-size: 24px;
    color: $grey;
    @media (max-width:400px) {
      font-size: 18px;
    }
  }

  // .orgo__img

  &__img {
  }
}
.eco {

  // .eco__section

  &__section {
  }

  // .eco__container

  &__container {
  }

  // .eco__sub-title

  &__sub-title {
    font-size: 16px;
    font-weight: 300;
    color: $grey;
    margin-bottom: 15px;
    @media (max-width:400px) {
      font-size: 10px;
    }
  }

  // .eco__title

  &__title {
    line-height: 1;
    margin-bottom: 25px;
    font-size: 36px;
    font-weight: 700;
    @media (max-width:400px) {
      font-size: 24px;
    }
  }
}
