.title__banner{
  width: 100%;
  height: 782px;
  background: url('../img/banner.png') 0 0 no-repeat local;
  background-position: center;
  position: relative;
  z-index: 2;
}

.main {

  // .main__title

  &__title {
    color: #fff;
    font-size: 48px;
    text-align: center;
    margin-bottom: 120px;
    span{
      color: $yellow;
    }
    @media (max-width:945px) {
      margin-bottom: 85px;
      font-size: 25px;
    }
    @media (max-width:550px){
      margin-bottom: 120px;
    }
  }
}
