.agrar {
  .eco__title{
    margin-bottom: 60px;
  }
  // .agrar__inner

  &__inner {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 15px;
    grid-row-gap: 15px;
    @media (max-width:1215px) {
      grid-template-columns: 1fr;
      align-items: center;
    }
  }

  // .agrar__block

  &__block {
    width: 598px;
    
    cursor: pointer;
    overflow: hidden;
    border-radius: 10px;
    filter: drop-shadow(0px 20px 50px rgba(0, 0, 0, 0.1));
    @media (max-width:645px){
      width: 330px;
      margin: 0 auto;
      height: 220px;
    }
    @media (max-width:1215px){

    }
  }
  .agrar__block:hover .agrar__img img{
    transition-duration: 0.3s;
    transform: scale(1.1);
    
  }
  .ag-img{
    @media (max-width:645px){
      display: none;
    }
  }
  // .agrar__img

  &__img {
    position: relative;
    .ag-img{
      transition-duration: 0.3s;
      width: 600px;
    }
    
  }
 .back-mob{
   @media (min-width:645px) {
     display: none;
   }
 }
  // .agrar__block-content

  &__block-content {
    width: 100%;
    position: absolute;
    top: 105px;
    left: 0;
    padding: 25px;
    @media (max-width:1000px) {
      top: 50px;
    }
    @media (max-width:645px){
      top: 30px;
    }
  }

  // .agrar__block-logo

  &__block-logo {
    margin-bottom: 30px;
  }

  // .agrar__text-inner

  &__text-inner {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  // .agrar__text

  &__text {
    font-size: 18px;
    font-weight: 500;
    color:#fff;
    @media (max-width:645px) {
      font-size: 14px;
    }
  }

  // .agrar__arrow

  &__arrow {
    margin-top: 70px;
    @media (max-width:645px){
      margin-top: 40px;
    }
  }
}
