.write {

  // .write__me

  &__me {
    position: absolute;
    z-index: 99;
    width: 100%;
    height: 100vh;
    padding: 0 5px;
    background-color: rgba(0, 0, 0, 0.322);
    display: none;
    &.active{
      display: block;
    }
  }

  // .write__inner

  &__inner {
    display: flex;
    justify-content: space-between;
    max-width: 750px;
    height: 535px;
    background-color: #fff;
    margin: 0 auto;
    margin-top: 295px;
    border-radius: 10px;
    @media (max-width:749px){
      padding: 0 30px;
    }
  }

  // .write__img

  &__img {
    img{
      width: 100%;
    }
    @media (max-width:749px) {
      display: none;
    }
    //flex: 0 0 50%;
  }

  // .write__forms

  &__forms {
    flex: 1 0 305px;
    margin-right: 35px;
    @media (max-width:749px){
      flex: 0 0 100%;
    }
  }

  // .write__text-inner

  &__text-inner {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    align-items: center;
    margin-bottom: 30px;
  }

  // .write__title

  &__title {
    font-size: 24px;
    font-weight: 700;
    font-style: normal;
   
    line-height: 1;
  }

  // .write__form

  &__form {
  }

  // .write__input-inner

  &__input-inner {
    
  }

  // .write__input

  &__input {
    resize:none;
    width: 100%;
    height: 65px;
    border: none;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.06);
    padding-left: 20px;
    margin-bottom: 20px;
    &:focus{
      outline: none;
    }
    &::placeholder{
      font-size: 14px;
      font-family: "Montserrat";
    }
    
  }
  &__area{
    height: 150px;
  }
}

.write__del{
  cursor: pointer;
}
