  &__button-cont {
  }

  // .eco__button-wrapper

  &__button-wrapper {
  }

  // .eco__button-text

  &__button-text {
  }
}
.write__form{
  .eco__button-cont{
    width: 305px;
    height: 74px;
    border-radius: 37px;
  }
  .eco__button-wrapper{
    width: 298px;
    height: 70px;
    border-radius: 35px;
  }
}

.info2 {
    position: fixed;
    z-index: 99;
    width: 100%;
    height: 100vh;
    padding: 0 5px;
    background-color: rgba(0, 0, 0, 0.322);
    display: none;
    &.active{
      display: block;
    }
  // .info__inner

  &__inner {
    max-width: 750px;
    height: 700px;
    margin: 0 auto;
    margin-top: 200px;
    background-color: #fff;
    border-radius: 10px;
    @media (max-width:645px){
      margin-top: 50px;
      
    }
  }

  // .info__img

  &__img {
    position: relative;
  }

  // .info__conten

  &__conten {
    position: absolute;
    top: 0;
    left: 0;
    padding: 0 30px;
    margin-top: 35px;
    display: flex;
    width: 100%;
  }

  // .info__del

  &__del {
    margin-left: auto;
    cursor: pointer;
  }

  // .info__title

  &__title {
    font-size: 30px;
    font-weight: 500;
    color: #fff;
    @media (max-width:645px) {
      font-size: 20px;
    }
  }

  // .info__text-inner

  &__text-inner {
    margin-top: 30px;
    padding: 0 20px;
  }

  // .info__text

  &__text {
    font-size: 14px;
    font-weight: 400;
    color: $grey;
    font-family: "Montserrat";
  }
}
