  margin-bottom: 50px;
  .eco__title{
    margin-bottom: 70px;
  }
  // .footer__inner

  &__inner {
    display: flex;
    justify-content: space-between;
    @media (max-width:1000px) {
      flex-direction: column;
      
    }
  }

  // .footer__block

  &__block {
    margin-bottom: 35px;
  }

  // .footer__block-img

  &__block-img {
    margin-right: 5px;
  }

  // .footer__block-img-inner

  &__block-img-inner {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }

  // .footer__title

  &__title {
    font-size: 24px;
    font-weight: 700;
    color: $black;
  }
}
