.reco {
  .eco__title{
    margin-bottom: 70px;
  }
  // .reco__inner

  &__inner {
    display: flex;
    flex-wrap: wrap;
  }

  // .reco__block

  &__block {
    max-width: 385px;
    height: 442px;
    box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
    margin-right: 20px;
    margin-bottom: 40px;
    &:nth-child(3){
      margin-right: 0;
    }
    border-radius: 10px;
  }

  // .reco__block-img

  &__block-img {
  }

  // .reco__block-text

  &__block-text {
    padding: 30px;
    
  }

  // .reco__title

  &__title {
    font-weight: 700;
    font-size: 24px;
    color: $black;
    margin-bottom: 30px;
    line-height: 1;
  }
}
