.video {

  // .video__inner

  &__inner {
    display: flex;
    align-items: center;
    @media (max-width:1100px){
      flex-direction: column-reverse;
    }
  }

  // .video__video
 
  &__video {
    
    background-color: $grey;
    position: relative;
    margin-right: 70px;
    max-width: 700px;
    filter: drop-shadow(0px 20px 50px rgba(0, 0, 0, 0.1));
    video{
      width: 700px;
      height: 430px;
      @media (max-width:780px){
        width: 500px;
        height: 300px;
      }
      @media (max-width:550px){
        width: 400px;
        height: 250px;
      }
      @media (max-width:460px){
        width: 300px;
        height: 200px;
      }
    }
    @media (max-width:1135px) {
      margin-right: 35px;
    }
    @media (max-width:1100px){
      margin-right: 0;
    }
    
  }
  &__play{
    position: absolute;
    width: 70px;
    height: 70px;
    left: 0;
    right: 0;
    top: 50%;
    margin: 0 auto;
    margin-top: -35px;
    cursor: pointer;
  }
}
