.compound {
  .eco__text{
    max-width: 402px;
    margin-top: 15px;
  }
  .eco__title{
    margin-right: 70px;
  }
  // .compound__text-inner

  &__text-inner {
    display: flex;
    margin-bottom: 70px;
    @media (max-width:835px) {
      flex-direction: column;
    }
  }

  // .compound__title

  &__title {
  }

  // .compound__text

  &__text {
  }

  // .compound__block-inner

  &__block-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    
    
  }
  &__block-inner-g{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-row-gap: 10px;
    grid-column-gap: 10px;
    @media (max-width:1215px) {
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr 1fr;
      grid-row-gap: 30px;
    }
    @media (max-width:925px) {
      grid-template-columns: 1fr 1fr;
    }
  }
  // .compound__block

  &__block {
    width: 288px;
    height: 161px;
    background-color: $green;
    border-radius: 10px;
    padding: 30px;
    position: relative;
    box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    @media (max-width:635px) {
      width: 158px;
      padding: 15px;
    }
  }

  .compound__block:hover .compound__block-info{
    display: block;
  }

  // .compound__elem

  &__elem {
    font-size: 40px;
    font-family: "Montserrat";
    color: #fff;
    font-weight: 500;
  }

  // .compound__elem-img

  &__elem-img {
  }

  // .compound__amount

  &__amount {
    font-size: 24px;
    font-weight: 700;
    color:#CAFFD9;
  }
}

.compound {

  // .compound__block-info

  &__block-info {
    position: absolute;
    width: 228px;
    height: 50%;
    display: none;
    background-color: $green;
    @media (max-width:635px) {
      width: 128px;
      height: 90%;
    }
  }

  // .compound__block-text

  &__block-text {
    color: #fff;
    font-size: 12px;
    font-weight: 500;
    font-family: "Montserrat";
    
    a{
      color: #fff;
      
    }
  }
}


.white {

  // .white__block-text

  &__block-text {
    color: $black;
    a{
      color:$green;
    }
  }

  // .white__elem

  &__elem {
    color: $grey;
  }

  // .white__elem-img

  &__elem-img {
  }

  // .white__amount

  &__amount {
    color: $yellow;
  }

  &__block-info{
    background-color: #fff;
  }
  &__block{
     background-color: #fff;
  }
}

