.header {
  
  padding-top: 15px;
  max-width: 1215px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  margin-bottom: 175px;
  @media (max-width:945px){
    margin-bottom: 80px;
  }
  
  // .header__inner

  &__inner {
    .eco__button-cont{
      @media (max-width:550px) {
        display: none;
      }
    }
    display: flex;
    
    height: 90px;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    opacity: 0.9;
    padding: 0 40px;
    border-radius: 45px;
    width: 100%;
    box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
    z-index: 3;
    @media (max-width:550px){
      height: 54px;
    }
  }

  // .header__menu

  &__menu {
      @media (max-width:1100px){
      position: fixed;
      width: 100%;
      height: 100vh;
      top: 0;
      left: -100%;
      background-color: #fff;
      opacity: 1;
      transition-duration: 0.3s;
      display: block;
      &.active{
        left: 0;
        
        .header__list{
          margin-left: 40px;
        }
        .header__list + .header__list{
          margin-top: 20px;
        }
      }
      .header__lists{
        flex-direction: column;
        margin-top: 120px;
      }
    }
    
  }

  &__menu-mobile{
    display: none;
    
  }
  // .header__lists

  &__lists {
    display: flex;

  }
 
  .header__list{
    
  }

  .header__list + .header__list {
    margin-left: 25px;
  }
  // .header__link

  &__link {
    text-decoration: none;
    color: $black;
    font-weight: 500;
    font-family: "FuturaPT";
    font-weight: 500;
    font-size: 18px;
    &:hover{
      color: $yellow;
    }
  }
}
.eco {

  // .eco__logo

  &__logo {
    position: relative;
    z-index: 2;
    @media (max-width:649px) {
      max-width: 200px;
    }
  }

  // .eco__button-cont

  &__button-cont {
    
    width: 206px;
    height: 50px;
    background: linear-gradient(94.14deg, #FFC522 8.37%, #FFBE09 90.95%);
    box-shadow: 0px 4px 20px rgba(117, 84, 65, 0.2);
    border-radius: 25px;
    display: flex;
    justify-content: center;
    transition-duration: 0.3s;
    &:hover{
      background: #FFD04A;
    }
    &:hover .eco__button-wrapper{
      background: #FFD04A;
    }
    
  }

 
    .header-button {
      display: none;
      @media (max-width:550px){
      position: absolute;
      top: 400px;
      left: -100%;
      display: block;
      &.active{
      left: 0;
      }
    }
  }
  // .eco__button-wrapper

  &__button-wrapper {
    width: 201px;
    height: 47px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(94.14deg, #FFC522 8.37%, #FFBE09 90.95%);
    text-decoration: none;
    border-radius: 25px;
    border: 1px solid #F6B80A;
    color: $brown;
    transition-duration: 0.3s;
    &:hover{
      background-color: #FFD04A;
    }
    &:hover .eco__button-cont{
      background-color: #FFD04A;
    }
  }

  // .eco__button-text

  &__button-text {
  }
}

.burger{
width: 30px;
height: 23px;
position: relative;
cursor: pointer;
transition-duration: 0.3s;
display: none;
@media (max-width:1100px) {
  display: block;
}
  &::before, &::after{
    position: absolute;
    content: '';
    width: 30px;
    height: 3px;
    border-radius: 5px;
    background-color: $yellow;
    transition-duration: 0.3s;
  }
  &::before{
    top: 0;
  }
  &::after{
    bottom: 0;
  }
  span{
    position: absolute;
    width: 30px;
    height: 3px;
    border-radius: 5px;
    background-color: $yellow;
    top: 50%;
    margin-top: -1px;
    transition-duration: 0.3s;
  }
  &.active{
    span{
      transform: scale(0);
    }
    &::before{
      top: 50%;
      margin-top: -1px;
      transform: rotate(-45deg);
    }
    &::after{
      top: 50%;
      margin-top: -1px;
      transform: rotate(45deg);
    }
  }
}
