.tab {
  .eco__text{
    max-width: 400px;
  }
  // .tab__inner

  &__inner {
    
  }

  // .tab__container

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  // .tab__title

  &__title {
    font-size: 36px;
    font-weight: 500;
    color: $black;
    @media (max-width:1000px) {
      font-size: 25px;
    }
  }

  // .tab__arrow

  &__arrow {
    &.active{
      transform: rotate(180deg);
    }
  }

  // .tab__content

  &__content {
    overflow: hidden;
    transform: translateY(-0px);
    height: 0px;
    border-bottom: 1px solid black;
    transition-duration: 0.5s;
    display: flex;
    align-items: center;
    @media (max-width:1100px){
      flex-direction: column;
    }
    &.active{
      transition-duration: 0.5s;
      transform: translateY(0);
      height:670px;
      @media (max-width:1100px){
        height: 1200px;
      }
    }
  }
  
}
.eco {

  // .eco__section

  &__section {
  }

  // .eco__container

  &__container {
  }
}

.tab {

  // .tab__tabble

  &__tabble {
    margin-top: 30px;
    transform: translateX(-40px);
    img{
      max-width: 1000px;
      @media (max-width:680px){
        max-width: 500px;
      }
      @media (max-width:415px){
        max-width: 400px;
      }
    }
    @media (max-width:1100px){
      transform: translateX(0px);
    }
    
  }

  // .tab__text-inner

  &__text-inner {
    margin-bottom: 30px;
   
  }

  // .tab__logo

  &__logo {

    img{
      width: 400px;
    }
    margin-bottom: 50px;
  }
}
