.prep {
  .swiper-pagination-bullet{
    background-color:#EBEBEB ;
    opacity: 1;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    margin-left: 15px;
    transition-duration: 0.3s;
  }
  .swiper-pagination-bullet:first-child{
    margin: 0;
  }
  .swiper-pagination-bullet-active{
    background-color:$yellow;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    transform: translateY(5px);
  }
  .swiper-slide{
    transform: scale(0.8);
    transition-duration: 0.5s;
  }
  .swiper-slide-active{
    transform: scale(1.2);
    @media (max-width:900px) {
      transform: scale(1);
    }
  }
  .eco__title{
    text-align: center;
  }
  .eco__sub-title{
    text-align: center;
  }
  .eco__text{
    text-align: center;
    
  }
  // .prep__slider

  &__slider {
  }

  // .prep__slider-wrapper

  &__slider-wrapper {
    padding-top: 70px;
  }

  // .prep__slider-slede

  &__slider-slede {
    
  }

  // .prep__slider-cont

  &__slider-cont {
    @media (max-width:900px) {
      margin-left: 190px;
    }
    @media (max-width:790px) {
      margin-left: 130px;
    }
    @media (max-width:700px) {
      margin-left: 40px;
    }
    @media (max-width:650px) {
      margin-left: 10px;
    }
    @media (max-width:520px) {
      margin-left: 0px;
    }
  }

  // .prep__slider-control

  &__slider-control {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  // .prep__slider-prev

  &__slider-prev {
    margin-right: 30px;
    cursor: pointer;
  }

  // .prep__slider-pug

  &__slider-pug {
  }

  // .prep__slider-next

  &__slider-next {
    margin-left: 30px;
    cursor: pointer;
  }
}
