.main {

  // .main__swiper

  &__swiper {
    
    .slider-container{
      
    }
    .swiper-container{
      
    
    }
    .swiper-pagination-bullet{
      background-color:#EBEBEB ;
      opacity: 1;
      width: 10px;
      height: 10px;
      border-radius: 5px;
      margin-left: 15px;
      transition-duration: 0.3s;
    }
    .swiper-pagination-bullet:first-child{
      margin: 0;
    }
    .swiper-pagination-bullet-active{
      background-color:$yellow;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      transform: translateY(5px);
    }
    .pagi{
      
    }
    
    .swiper-button-next:after, 
    .swiper-button-prev:after{
      display: none;
    }
    .swiper-button__row{
      
    }
    .swiper-button-next, 
    .swiper-button-prev{
      width: 50px;
      height: 50px;
      background-repeat: no-repeat;
      background-color:#FDF5E6FF; 
      border-radius: 50%;
      position: static;
      display: inline-block;
      margin-top: 20px;
    }
    .swiper-button-prev{
      background-image: 
      url(../img/prev.svg) ;
    
    }
    
    .swiper-button-next{
      
    }
    *:focus {
      outline: none;
    }
    
    
    .swiper2{
      
    }
  }

  // .main__slider

  &__slider {
    margin-bottom: 60px;
  }

  // .main__slider-wrapper

  &__slider-wrapper {
  }

  // .main__slider-slide

  &__slider-slide {
    padding-top: 40px;
    @media (max-width:945px) {
      margin-left: 50px;
    }
    @media (max-width:880px) {
      margin-left: 30px;
    }
    @media (max-width:825px) {
      margin-left: 20px;
    }
    @media (max-width:650px) {
      margin-left: 30px;
    }
    @media (max-width:580px){
      margin-left: 20px;
    }
    @media (max-width:550px){
      margin-left: 10px;
    }
    @media (max-width:480px){
      margin-left: 0;
    }
    @media (max-width:455px){
      margin-left: 20px;
    }
    @media (max-width:455px){
      margin-left: 10px;
    }
    @media (max-width:400px){
      margin-left: 0;
    }
  }

  // .main__slider-control

  &__slider-control {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  // .main__slider-button-prev

  &__slider-button-prev {
    margin-right: 30px;
    cursor: pointer;
  }

  // .main__slider-pugination

  &__slider-pugination {
    
  }

  // .main__slider-button-next

  &__slider-button-next {
    margin-left: 30px;
    cursor: pointer;
  }
}
.swiper-container {
}
.swiper-wrapper {
}
.swiper-slide {
}

.main {

  // .main__slide-contet

  &__slide-contet {
    width: 289px;
    height: 163px;
    background-color: #fff;
    border-radius: 10px;
    position: relative;
    @media (max-width:650px){
      max-width: 198px;
      
    }
    @media (max-width:455px){
      max-width: 158px;
    }
    @media (max-width:365px){
      max-width: 100%;
    }
  }

  // .main__slide-img

  &__slide-img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    box-shadow: 0px 4px 20px rgba(117, 84, 65, 0.2);
    position: absolute;
    left: 50%;
    margin-left: -35px;
    top: -35px;
    @media (max-width:650px){
      width: 50px;
      height: 50px;
      top: -25px;
      margin-left: -25px;
    }
  }

  // .main__slide-text-inner

  &__slide-text-inner {
  }

  // .main__slide-text

  &__slide-text {
    text-align: center;
    padding-top: 50px;
    @media (max-width:650px){
      padding-top: 20px;
    }
  }
}
.main__slider-overflow{
  
  border-bottom-left-radius: 45%;
  border-bottom-right-radius: 45%;
  max-width: 1215px;
  height: 200px;
  margin: 0 auto;
  overflow: hidden;
  margin-bottom: 50px;
  @media (max-width:945px){
    height: 400px;
    border-bottom-left-radius: 40%;
    border-bottom-right-radius: 40%;
  }
  @media (max-width:650px) {
    border-bottom-left-radius: 32%;
    border-bottom-right-radius: 32%;
  }
}
.swiper-container-multirow-column > .swiper-wrapper {
  flex-direction: column;
}
