.eco{
  font-family: "FuturaPT";
  position: relative;
}

.eco__container{
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
  @media (max-width:500px) {
    padding: 0 5px;
  }
  position: relative;
  z-index: 3;
}

.lock{
  overflow: hidden;
}
.eco__section{
  margin-bottom: 120px;
  position: relative;
  z-index: 2;
}

.eco {


  &__sub-title {
    font-size: 16px;
    font-weight: 300;
    color: $grey;
    margin-bottom: 15px;
    @media (max-width:475px) {
      font-size: 10px;
    }
  }

  // .eco__title

  &__title {
    line-height: 1;
    margin-bottom: 25px;
    font-size: 36px;
    font-weight: 700;
    @media (max-width:475px) {
      font-size: 24px;
    }
  }

  &__text {
    font-size: 14px;
    font-weight: 400;
    color: $grey;
    font-family: "Montserrat";
  }
}
.info-back{
  position: absolute;
  z-index: 3;
  width: 720px;
}